import SparkMD5 from "spark-md5";
// console.log(self);
self.onmessage = (e) => {
  const spark = new SparkMD5();
  spark.append("world hello");
  //spark.append("hello");
  console.log("worker onmessage", e.data);
  self.postMessage(spark.end());
  self.close();
};
